<template>
  <div class="header-container">

    <div class="l-content">

      <div style="display: flex; align-items: center" @click="handleMenuSelect('/home')">
        <img style="width: 32px; height: 32px; margin: 16px" src="../assets/logo.png"/>
        <h3>AppLocalize</h3>
      </div>
      <el-menu class="el-menu-demo" :default-active="activeIndex" mode="horizontal" @select="handleMenuSelect">

<!--        <el-menu-item index="/pricing">Pricing</el-menu-item>-->

        <el-menu-item index="/translator">Translate</el-menu-item>

<!--        <el-menu-item index="/docs/guide/" @click="handleDocs()" >Docs</el-menu-item>-->

<!--        <el-menu-item index="/support">Support</el-menu-item>-->


      </el-menu>

    </div>
    <div class="r-content">

      <el-button type="text" @click="handleToConsole">Go to console</el-button>

<!--      <el-button type="text" @click="handleLogin">{{ loginButton }}</el-button>-->

      <el-dialog title="Login" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="Email">
            <el-input v-model="form.email"></el-input>
          </el-form-item>
          <el-form-item label="Password">
            <el-input type="password" v-model="form.password"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">Cancel</el-button>
          <el-button type="primary" @click="submit">Login</el-button>
        </div>
      </el-dialog>

    </div>
  </div>
</template>

<script>
import Cookie from 'js-cookie'
import {login} from '../api/index'

export default {
  data() {
    return {
      dialogFormVisible: false,
      formLabelWidth: '80px',
      form: {
        email: '',
        password: '',
      },
    }
  },
  methods: {
    handleToConsole() {
      const token = Cookie.get("token")
      const name = Cookie.get("name")
      if (token != null && name != null) {
        this.$router.push('/projects')
      } else {
        this.dialogFormVisible = true
      }
    },
    handleLogin() {
      const token = Cookie.get("token")
      const name = Cookie.get("name")
      if (token != null && name != null) {
        return
      }
      this.dialogFormVisible = true
    },
    handleDocs() {
      window.location.href = 'https://applocalize.top/docs/guide'
    },
    submit() {
      console.log(this.form);
      login(this.form).then((data) => {
        if (data.code == 200) {
          this.dialogFormVisible = false
          Cookie.set("token", data.data.token)
          Cookie.set("name", data.data.name)
        }
        console.log(data);
      })

    },
    handleMenuSelect(keyPath) {
      if (this.$route.path != keyPath) {
        this.$router.push(keyPath)
      }
    }
  },
  computed: {
    loginButton() {
      const token = Cookie.get("token")
      const name = Cookie.get("name")

      if (token != null && name != null) {
        return name
      }
      return "Login"
    },
    activeIndex() {
      return this.$route.path
    }
  }
}
</script>

